.thank-section {
	position: relative;
	text-align: center;
	.orn-1 {
		position: absolute;
		top: 1rem;
		left: 0;
		width: 240px;
		transform: translate(-60%, 0) rotate(-20deg);
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 280px;
		}
		img {
			transform-origin: 10% 90%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 2rem;
		right: 0;
		width: 240px;
		transform: translate(60%, 0) rotate(20deg) scaleX(-1);
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 280px;
		}
		img {
			transform-origin: 10% 90%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 240px;
		transform: translate(-70%, 30%);
		@include min-md {
			width: 260px;
			transform: translate(-60%, 30%);
		}
		@include min-lg {
			width: 280px;
		}
		.orn-4 {
			position: absolute;
			top: 50%;
			right: 0;
			width: 240px;
			transform: translate(10%, -15%);
			@include min-md {
				width: 260px;
			}
			@include min-lg {
				width: 280px;
			}
			img {
				transform-origin: 10% 90%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 240px;
		transform: translate(70%, 30%);
		@include min-md {
			width: 260px;
			transform: translate(60%, 30%);
		}
		@include min-lg {
			width: 280px;
		}
		.orn-6 {
			position: absolute;
			top: 50%;
			left: 0;
			width: 240px;
			transform: translate(-10%, -15%) scaleX(-1);
			@include min-md {
				width: 260px;
			}
			@include min-lg {
				width: 280px;
			}
			img {
				transform-origin: 10% 90%;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/7;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
		.thank {
			position: relative;
			padding: 3rem 2rem;
			color: var(--color-light);
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: var(--color-primary);
				border-radius: var(--border-radius);
				opacity: 0.5;
				content: "";
			}
			@include max-md {
				padding: 2rem 1rem 5rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
