.reminder-wrap {
	position: relative;
	display: block;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-40%, -20%);
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 240px;
		}
		img {
			transform-origin: 10% 90%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 200px;
		transform: translate(45%, -30%) scaleX(-1);
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
		img {
			transform-origin: 10% 90%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 240px;
		transform: translate(-68%, 0);
		@include min-md {
			width: 260px;
			transform: translate(-63%, 0);
		}
		@include min-lg {
			width: 280px;
		}
		.orn-4 {
			position: absolute;
			top: 70%;
			right: 0;
			width: 160px;
			transform: translate(0%, -50%) rotate(-20deg);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
			img {
				transform-origin: 10% 90%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 240px;
		transform: translate(72%, 0);
		@include min-md {
			width: 260px;
			transform: translate(65%, 0);
		}
		@include min-lg {
			width: 280px;
		}
		.orn-6 {
			position: absolute;
			top: 70%;
			left: 0;
			width: 160px;
			transform: translate(0%, -50%) scaleX(-1) rotate(-20deg);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
			img {
				transform-origin: 10% 90%;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 160px;
		transform: translate(-20%, 70%);
		@include min-md {
			width: 180px;
			transform: translate(0%, 70%);
		}
		@include min-lg {
			width: 200px;
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 160px;
		transform: translate(20%, 65%) scaleX(-1);
		@include min-md {
			width: 180px;
			transform: translate(0%, 65%) scaleX(-1);
		}
		@include min-lg {
			width: 200px;
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100px;
		transform: translate(-30%, 30%);
		@include min-md {
			width: 120px;
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100px;
		transform: translate(30%, 30%) scaleX(-1);
		@include min-md {
			width: 130px;
		}
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		padding: 5rem;
		margin: 0 auto;
		z-index: 2;
		@include max-md {
			padding: 3rem;
			width: 100%;
		}
		.elements-wrapper-absolute {
			border-radius: var(--border-radius);
			overflow: hidden;
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
