.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.elements-wrapper-absolute {
		.background-element {
			img {
				@include max-lg {
					object-position: 40% 50%;
				}
				@include max-md {
					object-position: 35% 50%;
				}
			}
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-40%, 0);
		@include min-md {
			width: 240px;
		}
		@include min-lg {
			width: 280px;
		}
		img {
			transform-origin: 10% 90%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 200px;
		transform: translate(45%, -10%) scaleX(-1);
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 300px;
		}
		img {
			transform-origin: 10% 90%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 280px;
		transform: translate(-68%, 0);
		@include min-md {
			width: 320px;
			transform: translate(-63%, 0);
		}
		@include min-lg {
			width: 340px;
		}
		.orn-4 {
			position: absolute;
			top: 70%;
			right: 0;
			width: 200px;
			transform: translate(20%, -50%) rotate(-20deg);
			@include min-md {
				width: 240px;
			}
			@include min-lg {
				width: 260px;
			}
			img {
				transform-origin: 10% 90%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 320px;
		transform: translate(72%, 0);
		@include min-md {
			width: 340px;
			transform: translate(65%, 0);
		}
		@include min-lg {
			width: 360px;
		}
		.orn-6 {
			position: absolute;
			top: 70%;
			left: 0;
			width: 200px;
			transform: translate(-20%, -50%) scaleX(-1) rotate(-20deg);
			@include min-md {
				width: 240px;
			}
			@include min-lg {
				width: 260px;
			}
			img {
				transform-origin: 10% 90%;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 240px;
		transform: translate(-20%, 70%);
		@include min-md {
			width: 260px;
			transform: translate(0%, 70%);
		}
		@include min-lg {
			width: 280px;
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 240px;
		transform: translate(20%, 65%) scaleX(-1);
		@include min-md {
			width: 260px;
			transform: translate(0%, 65%) scaleX(-1);
		}
		@include min-lg {
			width: 280px;
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 120px;
		transform: translate(-30%, 30%);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 120px;
		transform: translate(30%, 30%) scaleX(-1);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.header-content {
		position: relative;
		height: 100vh;
		.header {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 50%;
			height: 100%;
			margin: 0 auto;
			@include max-lg {
				width: 80%;
			}
			@include max-md {
				width: 100%;
			}
			.header-title {
				position: relative;
				text-align: center;
				color: var(--color-dark);
				&-content {
					position: relative;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
