.stream-section {
	position: relative;
	text-align: center;
	background-color: var(--color-primary);
	color: var(--color-light);
	.orn-1 {
		position: absolute;
		top: 3rem;
		left: 0;
		width: 240px;
		transform: translate(-60%, 0) rotate(-20deg);
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 280px;
		}
		img {
			transform-origin: 10% 90%;
		}
		.orn-2 {
			position: absolute;
			top: 100%;
			right: 0;
			width: 240px;
			transform: translate(-50%, -20%) rotate(20deg);
			@include min-md {
				width: 260px;
				transform: translate(-40%, -20%) rotate(20deg);
			}
			@include min-lg {
				width: 280px;
			}
			img {
				transform-origin: 45% 90%;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 4rem;
		right: 0;
		width: 240px;
		transform: translate(60%, 0) rotate(20deg) scaleX(-1);
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 280px;
		}
		img {
			transform-origin: 10% 90%;
		}
		.orn-4 {
			position: absolute;
			top: 100%;
			right: 0;
			width: 240px;
			transform: translate(-50%, 0) rotate(20deg);
			@include min-md {
				width: 260px;
				transform: translate(-40%, 0) rotate(20deg);
			}
			@include min-lg {
				width: 280px;
			}
			img {
				transform-origin: 45% 90%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 160px;
		transform: translate(-50%, 0);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
		img {
			transform-origin: 40% 90%;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 100px;
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
			img {
				transform-origin: 50% 50%;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100px;
		transform: translate(20%, 0) scaleX(-1);
		@include min-md {
			width: 120px;
		}
		@include min-lg {
			width: 140px;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.stream-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.stream-wrapper {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				padding: 3rem 2rem;
				background-color: var(--color-main);
				border-radius: var(--border-radius);
				color: var(--color-light);
				overflow: hidden;
				@include max-md {
					padding: 2rem 1rem;
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		position: relative;
		width: 100%;
	}
}
