.story-section {
	position: relative;
	.story {
		position: relative;
		padding: 5rem 0;
		background-color: var(--color-primary);
		color: var(--color-light);
		border-top-left-radius: 20px;
		border-top-right-radius: 20px;
		overflow: hidden;
		@include max-md {
			padding: 3rem 1.5rem;
		}
		.orn-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 240px;
			transform: translate(-70%, 0) rotate(-20deg);
			@include min-md {
				width: 260px;
				transform: translate(-50%, 0) rotate(-20deg);
			}
			@include min-lg {
				width: 280px;
			}
			img {
				transform-origin: 10% 90%;
			}
			.orn-2 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 120px;
				transform: translate(-45%, 20%);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 160px;
				}
				img {
					transform-origin: 50% 50%;
				}
			}
		}
		.orn-3 {
			position: absolute;
			bottom: 2rem;
			right: 0;
			width: 240px;
			transform: translate(75%, 0) scaleX(-1) rotate(-20deg);
			@include min-md {
				width: 260px;
				transform: translate(55%, 0) scaleX(-1) rotate(-20deg);
			}
			@include min-lg {
				width: 280px;
			}
			img {
				transform-origin: 10% 90%;
			}
			.orn-4 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 120px;
				transform: translate(-40%, 20%);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 160px;
				}
				img {
					transform-origin: 50% 50%;
				}
			}
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 320px;
			transform: translate(-50%, 60%) scaleX(-1);
			opacity: 0.25;
			@include min-md {
				width: 340px;
			}
			@include min-lg {
				width: 360px;
			}
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 320px;
			transform: translate(50%, 0) scaleX(-1);
			opacity: 0.25;
			@include min-md {
				width: 340px;
			}
			@include min-lg {
				width: 360px;
			}
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
