.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0 5rem;
	}
	.ship {
		position: absolute;
		width: auto;
		height: 100%;
		bottom: 0;
		left: 50%;
		transform: translate(-50%, 0);
		img {
			width: auto;
			height: 100%;
			opacity: 0.5;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 240px;
		transform: translate(-60%, 0);
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 280px;
		}
		img {
			transform-origin: 10% 90%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 2rem;
		right: 0;
		width: 240px;
		transform: translate(60%, 0) scaleX(-1);
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 280px;
		}
		img {
			transform-origin: 10% 90%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 160px;
		transform: translate(-50%, 40%);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
		img {
			transform-origin: 40% 90%;
		}
		.orn-4 {
			position: absolute;
			top: 30%;
			right: 0;
			width: 100px;
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
			img {
				transform-origin: 50% 50%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 160px;
		transform: translate(50%, 50%) scaleX(-1);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
		img {
			transform-origin: 40% 90%;
		}
		.orn-6 {
			position: absolute;
			top: 30%;
			right: 0;
			width: 100px;
			transform: translate(10%, 0);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
			img {
				transform-origin: 50% 50%;
			}
		}
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
