.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			display: none;
		}
		@include min-lg {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			display: block;
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin: 1rem 0;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		.orn-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 240px;
			transform: translate(-70%, 0) rotate(-20deg);
			@include min-md {
				width: 260px;
				transform: translate(-60%, 0) rotate(-20deg);
			}
			@include min-lg {
				width: 280px;
			}
			img {
				transform-origin: 10% 90%;
			}
			.orn-2 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 120px;
				transform: translate(-45%, 20%);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 160px;
				}
				img {
					transform-origin: 50% 50%;
				}
			}
		}
		.orn-3 {
			position: absolute;
			bottom: 2rem;
			right: 0;
			width: 240px;
			transform: translate(75%, 0) scaleX(-1) rotate(-20deg);
			@include min-md {
				width: 260px;
				transform: translate(65%, 0) scaleX(-1) rotate(-20deg);
			}
			@include min-lg {
				width: 280px;
			}
			img {
				transform-origin: 10% 90%;
			}
			.orn-4 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 120px;
				transform: translate(-40%, 20%);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 160px;
				}
				img {
					transform-origin: 50% 50%;
				}
			}
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 320px;
			transform: translate(-50%, 70%) scaleX(-1);
			opacity: 0.25;
			@include min-md {
				width: 340px;
				transform: translate(-40%, 65%) scaleX(-1);
			}
			@include min-lg {
				width: 360px;
			}
		}
		.orn-6 {
			position: absolute;
			top: 1rem;
			right: 0;
			width: 320px;
			transform: translate(50%, 0) scaleX(-1);
			opacity: 0.25;
			@include min-md {
				width: 340px;
				transform: translate(40%, 0) scaleX(-1);
			}
			@include min-lg {
				width: 360px;
				transform: translate(30%, 0) scaleX(-1);
			}
		}
		.orn-7 {
			position: absolute;
			top: 46%;
			left: 0;
			width: 320px;
			transform: translate(-50%, -50%);
			z-index: 2;
			@include min-md {
				top: 90%;
				width: 340px;
			}
			@include min-lg {
				width: 360px;
			}
			img {
				transform-origin: 10% 90%;
			}
		}
		.orn-8 {
			position: absolute;
			top: 97%;
			right: 0;
			width: 320px;
			transform: translate(50%, -50%) scaleX(-1);
			z-index: 2;
			@include min-md {
				top: 95%;
				width: 340px;
			}
			@include min-lg {
				width: 360px;
			}
			img {
				transform-origin: 10% 90%;
			}
		}
		.orn-9 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 80px;
			transform: translate(-10%, 10%);
			@include min-md {
				width: 100px;
			}
			.orn-10 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 200px;
				transform: translate(-20%, 0) rotate(-20deg);
				@include min-md {
					transform: translate(-10%, -20%) rotate(-20deg);
				}
				img {
					transform-origin: 10% 90%;
				}
			}
		}
		.orn-11 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 80px;
			transform: translate(10%, 0) scaleX(-1);
			@include min-md {
				width: 100px;
			}
			.orn-12 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100px;
				transform: translate(10%, 0);
				@include min-md {
					width: 120px;
				}
				@include min-lg {
					width: 140px;
				}
				img {
					transform-origin: 40% 100%;
				}
			}
		}
		.orn-13 {
			position: absolute;
			top: 0;
			left: 0;
			width: 140px;
			transform: translate(-30%, -40%) scaleX(-1);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
		}
		.orn-14 {
			position: absolute;
			top: 0;
			right: 0;
			width: 180px;
			transform: translate(20%, -50%);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
		}
		.orn-15 {
			position: absolute;
			top: 0;
			right: 0;
			width: 140px;
			transform: translate(20%, -30%);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
		}
		.orn-16 {
			position: absolute;
			top: 0;
			left: 0;
			width: 180px;
			transform: translate(-20%, -50%) scaleX(-1);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
		}
	}
	.couple-body {
		position: relative;
		padding: 5rem;
		@include max-md {
			padding: 3rem 0;
		}
		.ship {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 112%;
			transform: translate(-50%, 8%);
		}
	}
	.couple-content {
		position: relative;
		padding: 5rem 0;
		background-color: var(--color-primary);
		color: var(--color-light);
		border-bottom-right-radius: 20px;
		border-bottom-left-radius: 20px;
		z-index: 3;
		overflow: hidden;
		@include max-md {
			padding: 3rem 1.5rem;
		}
	}
	.couple {
		position: relative;
		text-align: center;
		background-color: var(--color-primary);
		color: var(--color-light);
		border-radius: var(--border-radius);
		padding: 0.25rem 1.25rem 2rem;
		margin-top: 18rem;
		z-index: 5;
		.image-wrap {
			width: 100%;
			position: relative;
			aspect-ratio: 1;
			margin: -10rem auto 0;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 500px;
				}
			}
			.frame {
				position: absolute;
				top: 50%;
				left: 50%;
				width: 150%;
				transform: translate(-50%, -60%);
				img {
					position: relative;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 3rem 0 0;
			p {
				margin-bottom: 0;
			}
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: absolute;
			bottom: -2rem;
			left: 50%;
			transform: translate(-50%, 50%);
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
