.wishes-section {
	position: relative;
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-40%, 0%) rotate(-20deg);
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 220px;
		}
		img {
			transform-origin: 10% 90%;
		}
		.orn-2 {
			position: absolute;
			top: 100%;
			right: 0;
			width: 320px;
			transform: translate(-20%, -10%) rotate(20deg);
			opacity: 0.3;
			@include min-md {
				width: 340px;
			}
			@include min-lg {
				width: 360px;
			}
			img {
				transform-origin: 40% 100%;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 240px;
		transform: translate(70%, 0);
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 280px;
		}
		img {
			opacity: 0.3;
		}
		img {
			transform-origin: 40% 100%;
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 120px;
			transform: translate(10%, 0) scaleX(-1);
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
			img {
				opacity: 1;
			}
			img {
				transform-origin: 50% 50%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-50%, 20%);
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 220px;
		}
		img {
			transform-origin: 40% 90%;
		}
		.orn-6 {
			position: absolute;
			bottom: 50%;
			right: 0;
			width: 240px;
			@include min-md {
				width: 260px;
			}
			@include min-lg {
				width: 280px;
			}
			img {
				transform-origin: 50% 90%;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(50%, 30%) scaleX(-1);
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 220px;
		}
		img {
			transform-origin: 40% 90%;
		}
		.orn-8 {
			position: absolute;
			bottom: 50%;
			right: 0;
			width: 240px;
			transform: scaleX(-1);
			@include min-md {
				width: 260px;
			}
			@include min-lg {
				width: 280px;
			}
			img {
				transform-origin: 50% 90%;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.wishes-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.wishes-form-wrapper {
		position: relative;
		padding: 5rem 0 2rem;
		background-color: var(--color-primary);
		color: var(--color-light);
		border-radius: 0 0 20px 20px;
		@include max-md {
			padding: 3rem 0 2rem;
		}
	}
	.card-form {
		margin-bottom: 0;
		background-color: transparent;
		position: relative;
		z-index: 3;
		&.card {
			border: none;
		}
		.card-body {
			padding: 2rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}

	.wishes-preview {
		position: relative;
		padding: 2rem 0 5rem;
		@include max-md {
			padding: 2rem 0 3rem;
		}
		.card {
			border: none;
			position: relative;
			background-color: transparent;
			border-radius: var(--border-radius);
			.card-body {
				overflow: hidden;
				padding: 2rem 0;
				.wishes {
					text-align: left;
					height: 32rem;
					overflow: auto;
					padding-right: 1rem;
					color: var(--color-dark);
					&::-webkit-scrollbar {
						width: 5px;
					}
					&::-webkit-scrollbar-track {
						background-color: #eee;
						border-radius: 20px;
						opacity: 0.15;
					}
					&::-webkit-scrollbar-thumb {
						background-color: var(--color-secondary);
						border-radius: 20px;
					}
					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						display: grid;
						grid-template-columns: auto 1fr;
						gap: 1rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
						.wish-badge {
							background-color: var(--color-light);
							width: 35px;
							aspect-ratio: 1;
							display: flex;
							justify-content: center;
							align-items: center;
							border-radius: 50px;
						}
						.wish-description {
							padding: 1rem;
							background-color: rgba($color: #fff, $alpha: 0.6);
							border-radius: 5px;
							position: relative;
							&::before {
								position: absolute;
								top: 15px;
								left: 0;
								width: 15px;
								height: 15px;
								transform: translateX(-100%);
								clip-path: url(#wishes-polygon);
								content: "";
								background-color: rgba($color: #fff, $alpha: 0.6);
							}
						}
					}
				}
			}
		}
	}
}
